import { StatCard } from "@/ui/StatCard";
import { FaPiggyBank,
    FaWallet,
    FaArrowUp,
    FaArrowDown} from "react-icons/fa";

export default function DashboardPage() {
    const dateNow = new Date().toLocaleDateString("id-ID", {
        weekday: "long",
        year: "numeric",
        month: "long",
        day: "numeric",
    });
    return (

     <div className="p-3 space-y-6">
        <div className="flex flex-col gap-8 text-white bg-gradient-to-r from-indigo-900 to-indigo-600 rounded-x1
        p-6">
            <div className="flex justify-between items-start flex-wrap gap-2">
                <div>
                    <h2 className="text-3xl font-semibold">Welcome back</h2>
                    <p className="text-md mt-1 font-normal">
                        insight at a glance : empowering your financial jouney   
                    </p>
                </div>
                <div className="text-right text-md text-white">
                    <p className="font-medium">
                        {dateNow}
                    </p>
                </div>
            </div>
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-4" >
                <StatCard
                    title="Total Balance"
                    value="2000"
                    icon={<FaWallet size={24} />}
                />
                <StatCard
                    title="Total saving"
                    value="5000"
                    icon={<FaPiggyBank size={24} />}
                    change="+10%"
                    color="text-green-500"
                />
                <StatCard
                    title="Total Expense"
                    value="3000"
                    icon={<FaArrowDown size={24} />}
                    change="-5%"
                    color="text-red-500"
                />
                <StatCard
                    title="Total Income"
                    value="7000"
                    icon={<FaArrowUp size={24} />}
                    change="+15%"
                    color="text-green-500"

                />
            </div>
        </div>
     </div>   
    )
}